import { QuizEntityQuestion } from './app/Model/QuizEntityQuestion';
import { HttpClient,HttpHeaders  } from '@angular/common/http';
import { Injectable } from '@angular/core';


@Injectable({
  providedIn: 'root'
})
export  class QuizService {
  seconds:number=0;
  timer;
  qnProgress: number;
  correctAnswerCount: number = 0;
  qns: QuizEntityQuestion[];
  answers:number[]=new Array(10);
  name:String="";


  constructor(private http : HttpClient) { }

//*****************Http Methods******************************** */

getQuestions() {
  console.log("inside the getquesitons");
  return this.http.get<QuizEntityQuestion>('https://quiz-back-end1997.herokuapp.com/questions');
}
displayTimeElapsed() {
  return Math.floor(this.seconds / 3600) + ':' + Math.floor((this.seconds /    60)%60) + ':' + Math.floor(this.seconds % 60);
}


getAnswers() {
  console.log("inside the getANswers");
  return this.http.get<QuizEntityQuestion>('https://quiz-back-end1997.herokuapp.com/answers');
}


}
