import { RegisterService } from './../../register.service';
import { QuizService } from './../../quiz.service';
import { Router } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { importExpr } from '@angular/compiler/src/output/output_ast';
import {Participant} from '../Model/QuizModelParticipant';

@Component({
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  isLinear = true;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  check=true;
    participant: Participant;
  errormessage:string;
  successmessage:string;

  constructor(public _formBuilder: FormBuilder,public regs:RegisterService,public router:Router,public quizService:QuizService) { }

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ['', Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', [Validators.required,Validators.email]]
    });
  }

submit(){
  this.successmessage=null;
  this.successmessage=null;
  this.participant=new Participant();
 this.participant.name=this.firstFormGroup.controls['firstCtrl'].value;
  this.participant.email=this.secondFormGroup.controls['secondCtrl'].value;
 this.participant.score=90;
 this.participant.time=800; 
 this.participant.pid=0;
 this.quizService.name=this.participant.name;
 this.regs.sendData(this.participant).subscribe(
  (success)=>{this.successmessage=success},
  (error)=>{this.errormessage=error},
  ()=>{this.router.navigate(['/quiz'])}
  
 );
 this.router.navigate(['/quiz'])
}


}
