import { QuizService } from './../../quiz.service';
import { Component, OnInit } from '@angular/core';
import { Router} from '@angular/router';
@Component({
  selector: 'app-quiz',
  templateUrl: './quiz.component.html',
  styleUrls: ['./quiz.component.css']
})
export class QuizComponent implements OnInit {

  constructor(public quizService:QuizService,public router:Router) { }
  flag:number=0;
  colorr1:string[]=new Array(10);
  colorr2:string[]=new Array(10);
  colorr3:string[]=new Array(10);
  colorr4:string[]=new Array(10);
  
  


  ngOnInit() {
   this.quizService.seconds=0;
    localStorage.setItem('seconds', this.quizService.seconds.toString());
       
    if (parseInt(localStorage.getItem('seconds'))>0) {
      
      this.quizService.seconds = parseInt(localStorage.getItem('seconds'));
      this.quizService.qnProgress = parseInt(localStorage.getItem('qnProgress'));
      this.quizService.qns = JSON.parse(localStorage.getItem('qns'));
      if (this.quizService.qnProgress == 100)
        this.router.navigate(['/result']);
      else
        this.startTimer();

      }
    else {
      console.log("inside the ngonit");
      this.quizService.seconds = 0;
      this.quizService.qnProgress = 0;
      this.quizService.getQuestions().subscribe(
        (data: any) => {
          this.quizService.qns = data;
          this.startTimer();
        }
      );
    }
    
  }
  

  
  startTimer() {
    this.quizService.timer = setInterval(() => {
      this.quizService.seconds++;
      localStorage.setItem('seconds', this.quizService.seconds.toString());
    }, 1000);
    
   
  }
  hello(){
    console.log("card clickable");
  }


  next(){
    this.quizService.qnProgress++;
    // this.colorr1=null;
    // this.colorr2=null;
    // this.colorr3=null;
    // this.colorr4=null;
  }

  prev(){

    this.quizService.qnProgress--;

  }
  color1(index:number){
    localStorage.setItem('qns', JSON.stringify(this.quizService.qns));
   
    console.log("color 4 clickable");
    console.log("index " +index);
    console.log(console);
    this.colorr1[index]="green";
    this.colorr2[index]=null;
    this.colorr3[index]=null;
    this.colorr4[index]=null;

    //*******************score logic************************** */
    this.quizService.answers[index]=1;


  }
  color2(index:number){
   
    console.log("color 4 clickable");
    this.colorr1[index]=null;
    this.colorr2[index]="green";
    this.colorr3[index]=null;
    this.colorr4[index]=null;
    this.quizService.answers[index]=2;

  }
  color3(index:number){
   
    console.log("color 4 clickable");
    this.colorr1[index]=null;
    this.colorr2[index]=null;
    this.colorr3[index]="green";
    this.colorr4[index]=null;
    this.quizService.answers[index]=3;

  }
  color4(index:number){
    localStorage.setItem('qnProgress', this.quizService.qnProgress.toString());
    
    console.log("color 4 clickable");
    this.colorr1[index]=null;
    this.colorr2[index]=null;
    this.colorr3[index]=null;
    this.colorr4[index]="green";
    this.quizService.answers[index]=4;

  }

  submit(){
    console.log(this.quizService.answers);
   
    clearInterval(this.quizService.timer);
    this.router.navigate(['/result']);



  }

}
